	import uniLoadMore from "../../components/uni-load-more.vue"
	let page = 0
  export default {
    data () {
      return {
        toId: 'item000',
        pageShow: false,
        categoryList: [],
        currIndex: 0,
        list: [],
        status: 0,
        page: 0,
        isLoadMore: false,
        isLoading: false,
        isMore: false
      }
    },

    onLoad () {
      page = 0
      this.getFightGroupCate()
      this.getFightGroupList(1)
    },

		components: {
			uniLoadMore
		},

    methods: {
      changeTab (index) {
        this.currIndex = index
        this.toId = `item${index}`
        page = 0
        this.list = []
        this.status = ''
        this.isLoadMore = false

        this.$nextTick(() => {
          this.getFightGroupList()
        })
      },

      getFightGroupCate () {
        this.$http.getFightGroupCate().then(res => {
          if (res.status === 200) {
            let data = res.data
            data.unshift({
              id: '',
              cate_name: '推荐'
            })
            this.categoryList = data
          }
        })
      },

      getFightGroupList (isFirst) {
        if (this.status === 'nomore' || this.status === 'loading') return

        if (page === 0) {
          this.$loading()
        }
				if (page > 0) {
					this.status = 'loading' 
        }
        
        this.$http.getFightGroupList({
          page: page + 1,
          cate_id: isFirst ? '' : this.categoryList[this.currIndex].id
        }).then(res => {
          if (res.status === 200) {
            page = page + 1

            if (!res.data.length) {
              this.status = 'nomore'
              return false
            }

					  this.list.push(...res.data)

            this.status = 'more'
            this.pageShow = true
          }
        })
      }
    }
  }
